<template>
  <dir>
    <form @submit="addTodo">
      <input type="text" v-model="title" name="title" placeholder="Add Todo...">
      <input type="Submit" value="SUBMIT" class="btn">
    </form>
  </dir>
</template>


<script>
import uuid from "uuid";
export default {
  name: "AddTodo",
  data() {
    return {
      title: ""
    };
  },
  methods: {
    addTodo(e) {
      e.preventDefault();
      const newTodo = {
        id: uuid.v4(), //data from jsonplaceholder has id, so we can delete this
        title: this.title, //from v-model="title" in template
        completed: false
      };
      //send up to parent by emit an event
      this.$emit("add-todo", newTodo);
      //emit event called "add-todo" and send the whole newTodo obj

      this.title = " "; //clear form in box after clicking submit
    }
  }
};
</script>


<style scoped>
form {
  display: flex;
}
input[type="text"] {
  flex: 10;
  padding: 5px;
}
input[type="submit"] {
  flex: 2;
}
</style>
