<template>
  <div id="app">
    <!--<Header/>-->
    <AddTodo v-on:add-todo="addTodo"/>
    <!--receive 'add-todo' from AddTodo and call "addTodo" function-->
    <Todos v-bind:todos="todos" v-on:delete-todo="deleteTodo"/>
    <!--"todos" refers to the todo under data() below-->
    <!--<img alt="Vue logo" src="./assets/logo.png"> -->
    <!--<HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <!--after receiving "delete-todo" from Todo.vue, call function "deleteTodo" -->
    <!--REMEMBER: the event we receive comes with a parameter(id) -->
  </div>
</template> 

<script>
//import HelloWorld from "./components/HelloWorld.vue";
//import Header from "../components/layout/Header";
import Todos from "../components/Todos";
import AddTodo from "../components/AddTodo";
import axios from "axios";

export default {
  name: "Home",
  components: {
    //Header,
    Todos,
    AddTodo
    //HelloWorld
  },
  data() {
    return {
      todos: [
        {
          id: 1,
          title: "Todo One",
          completed: false
        },
        {
          id: 2,
          title: "Todo Two",
          completed: true
        },
        {
          id: 3,
          title: "Todo Three",
          completed: false
        }
      ]
    };
  },
  methods: {
    deleteTodo(id) {
      axios
        .delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
        .then(res => (this.todos = this.todos.filter(x => x.id !== id)))
        .catch(err => console.log(err));
      //this.todos = this.todos.filter(x => x.id !== id);
    },
    addTodo(newTodo) {
      const { title, completed } = newTodo;
      axios
        .post("https://jsonplaceholder.typicode.com/todos", {
          title, //same as title = this.title
          completed
        }) //post to server with the new title and completed
        //after post it will give back a res with the new todo title, completed and the id it created
        .then(res => (this.todos = [...this.todos, res.data]))
        .catch(err => console.log(err));
      //receive obj(newTodo) from AddTodo.vue (with parameters: id, title, completed)
      //this.todos = [...this.todos, newTodo];
    }
  },
  created() {
    axios
      .get("https://jsonplaceholder.typicode.com/todos?_limit=5") //get request
      // to get only 10 items, add "?_limit=10" to the link
      .then(res => (this.todos = res.data))
      // since get request will return a promise, use then() to handle it
      // then() will return a res(response), which is the data from the jsonholder website, when the page loads
      // we then assign the data to our empty todos array (or overwrite if not empty)
      .catch(err => console.log(err));
  }
};
</script>

