<!--todos: [{ id: 1, title: "Todo One", completed: false },
            { id: 2, title: "Todo Two", completed: true },
            { id: 3, title: "Todo Three", completed: false } ]-->

<template>
  <div>
    <!--todos is from App.vue, each iteration of todos is an obj-->
    <div v-for="x in todos" v-bind:key="x.id">
      <TodoItem v-bind:todo="x" v-on:del-todo="$emit('delete-todo', x.id)"/>
      <!--pass x to TodoItem as todo (obj), ie, use todo in TodoItem, not x-->
      <!--receive del-todo event from TodoItem and emit an event called 'delete-todo'-->
    </div>
  </div>
</template>

<script>
import TodoItem from "./TodoItem";

export default {
  name: "Todos",
  components: {
    TodoItem
  },
  props: ["todos"] //the todos from App.vue is an array
};
</script>

