<template>
  <div class="todo-item" v-bind:class="{'is-complete':todo.completed}">
    <!--if todo.completed is true, bind the 'is-complete' class-->
    <p>
      <input type="checkbox" v-on:change="markComplete">
      {{todo.title}}
      <button @click="$emit('del-todo', todo.id)" class="del">X</button>
      <!--$emit: emit an event called 'del-todo' with parameter todo.id-->
      <!--ie, a number (the number of the taks you are deleting)-->
    </p>
  </div>
</template>

<script>
export default {
  name: "TodoItem",
  props: ["todo"], //todo from Todos.vue, each todo is an obj
  methods: {
    markComplete() {
      this.todo.completed = !this.todo.completed;
    }
  }
};
</script>

<style scoped>
.todo-item {
  background: #f4f4f4;
  padding: 10px;
  border-bottom: 1px #ccc dotted;
}
.is-complete {
  text-decoration: line-through;
}
.del {
  background: #ff0000;
  color: #fff;
  border: none;
  padding: 5px 9px;
  border-radius: 50%;
  cursor: pointer;
  float: right;
}
</style>

